@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');
:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}

*{
  margin:0;
  padding: 0;
}

body{
  font-family: 'Raleway';
}

.App{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

/* .condiv class is for the right side content container*/
.condiv{
  padding: 100px ;
  background-color: #f4f5f9;
  flex:var(--condiv-flex);
}

nav{
  padding: 60px 30px 0px 30px;
  flex:var(--sidebar-flex);
  /*flex-direction: row;*/
  background:rgb(81, 117, 218);
  background:rgb(95, 202, 245);
  /*linear-gradient(180deg, rgb(95, 202, 245) 70%,
  rgb(76, 255, 199) 90%);*/
}

nav ul {
  font-size:var(--fontsize-nav);
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}

nav ul li {
  margin: 40px;
}

.active {
  font-weight: bolder;
}

.social {
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}
.social i {
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}
.social i:hover {
  color: #ffffff;
}

.subtopic {
  margin: 10px 10px 30px 10px !important;
}

p{
  font-size:var(--fontsize-p);
}

h1,h2,h3,h4,p {
  margin: 10px;
}

.widecard{
  border:0px solid #9a9b9c;
  display: flex;
  margin: 30px 0px 30px 30px;
}

.widecard:hover {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.credits{
  position: fixed;
  bottom: 0;
  width: 100%;
  font-size:85%;
}
